// IE fix --------------------------------------------------

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .decription .popup .tooltip {
    display: block;
    visibility: hidden;
    opacity: 0;
    padding: 20px;
    position: absolute;
    top: -158px;
    left: -20px;
    width: 220px;
    background-color: $darkgrey;
    color: $white;
    font-size: 12px;
    text-align: center;
    line-height: 1.5;
    text-transform: none;
    transition: visibility 0s, opacity 0s linear;

    h2 {
      margin: 0;
    }

    p {
      font-size: 0.9rem;
      margin: 0;
    }

    span {
      width: 28px;
      height: 3px;
      background-color: $white;
      margin: 10px 0 10px;
    }

    .arrow {
      position: absolute;
      bottom: -40px;
      right: 88px;
      width: 0;
      height: 0;
      border-left: 40px solid transparent;
      border-right: 40px solid transparent;
      border-top: 40px solid $darkgrey;
    }
  }

  .Clients .crane {
    .tooltip {
      top: -223px;
      left: 15px;
      width: 220px;
    }
  }

  .Clients .beat {
    .tooltip {
      top: -158px;
      left: -63px;
      width: 220px;
    }
  }

  .Clients .northern {
    .tooltip {
      top: -223px;
      left: 40px;
      width: 220px;
    }
  }

  .google .popup {
    small {
      display: inline-block;
    }

    display: inline-block;
    vertical-align: middle;
    text-align: left;
    padding: 10px;
    position: absolute;
    left: -10px;
    top: -80px;
    color: $fadedgrey;
    font-size: 13px;
    width: 130px;
    height: 60px;
    background-color: $white;
    box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.1);
    z-index: 100;
    border: 1px solid rgba(0, 0, 0, 0.1);
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.2s ease-in;

    .arrow {
      position: absolute;
      bottom: -10px;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 10px solid $white;
      box-shadow: 0 0.3125rem 2.1875rem rgba(0, 0, 0, 0.1);
      z-index: 101;
    }
  }

  .card-img-top .category {
    position: absolute;
    top: 10px;
    right: 0;
    font-size: 12px;
    font-weight: 600;
    padding: 0 10px;
    color: $white;
    text-transform: uppercase;
    z-index: 51;

    .tooltip {
      display: inline-block;
      vertical-align: middle;
      text-align: center;
      visibility: hidden;
      opacity: 0;
      padding: 10px 20px;
      position: absolute;
      top: -65px;
      left: -68px;
      width: 150px;
      height: 30px;
      background-color: rgba(0, 0, 22, 0.651);
      font-weight: 500;
      font-size: 12px;
      text-align: center;
      line-height: 1.5;
      text-transform: none;
      transition: visibility 0s, opacity 0.2s linear;

      p {
        margin: 0;
        font-size: 10px;
      }

      .middle {
        padding-top: 8px;
      }

      .arrow {
        position: absolute;
        bottom: -10px;
        left: 90px;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 10px solid rgba(0, 0, 0, 0.651);
      }
    }
  }

  .navbar-nav {
    .web:hover {
      background-color: $lightpurple;
      border-right: 0.0625rem solid $lightpurple;
      border-left: 0.0625rem solid $lightpurple;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .IT:hover {
      background-color: $lightblue;
      border-right: 0.0625rem solid $lightblue;
      border-left: 0.0625rem solid $lightblue;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .telecoms:hover {
      background-color: $red;
      border-right: 0.0625rem solid $red;
      border-left: 0.0625rem solid $red;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .software:hover {
      background-color: $steelblue;
      border-right: 0.0625rem solid $steelblue;
      border-left: 0.0625rem solid $steelblue;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .marketing:hover {
      background-color: $lightgreen;
      border-right: 0.0625rem solid $lightgreen;
      border-left: 0.0625rem solid $lightgreen;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .security:hover {
      background-color: $rose;
      border-right: 0.0625rem solid $rose;
      border-left: 0.0625rem solid $rose;

      i {
        color: $white;
      }

      .strip {
        display: block;
        position: absolute;
      }
    }

    .strip .nav-menu {
      display: flex;
      position: static;
      text-transform: uppercase;
      font-size: 1rem;
      height: 2.8125rem;
      width: 73.125rem;
      color: $white;
      margin: 0 auto;
      list-style-type: none;

      li:not(:first-child) {
        padding-left: 30px;
      }
    }
  }
}

