// Fontfamilys ----------------------------
// font-family: 'Roboto', sans-serif;   ---
// font-family: 'Poppins', sans-serif;  ---
// Fontfamilys ----------------------------

@import "Partials/Attributes", "Partials/partners", "Partials/mediaq",
  "Partials/cookie", "Partials/contactForm", "Partials/clients",
  "Partials/latestnews", "partials/StickeyHeaderOnScroll", "Partials/header",
  "Partials/infocards", "Partials/footer", "Partials/culturesection",
  "Partials/navbar", "Partials/gallery", "Partials/livechat",
  "Partials/Mobilemenu";

//  font import

@import url(https://fonts.googleapis.com/css?family=Poppins|Roboto&display=swap);

* {
  // border: 1px solid;
  font-family: "Poppins", sans-serif;
}

html,
body {
  width: 100%;
  min-width: 580px;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow: hidden;
}

#viewport {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: 1;
  transition: transform 0.5s;
}

body {
  font-size: 1rem;
  letter-spacing: 0.025rem;
  margin: 0;
  line-height: 1.375rem;
}

.btn {
  border-radius: 0.1875rem;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.3125rem 0.9375rem;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

.table {
  display: table;
}

li {
  list-style-type: none;
}

ul {
  margin: 0;
  padding: 0;
}

small {
  font-size: 0.625rem;
}

// Clear fix

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

// fix header to browser and hide out of view

#header {
  background-color: white;
}

.overflow {
  z-index: 1000;
  transition: transform 0.5s;
}

.menu-slide {
  transform: translateX(-350px);
}
